}
.box .img-full-bg {
  position: relative;
  left: 50%;
  width: 1920px;
  max-width: none;
  height: 710px;
  margin-left: -960px;
}
.container {
  position: relative;
  max-width: 1220px;
  margin: 0 auto;
}
@media screen and (max-width: 720px) {
  #app {
    min-width: inherit;
  }
  .box {
    min-width: inherit;
  }
  .title {
    font-size: 20px;
  }
  .mobile {
    display: block;
  }
}
