}
.iconfont {
  display: inline-block;
  position: relative;
  color: #666;
  font-style: normal;
  font-size: 18px;
  font-family: 'iconfont';
  vertical-align: middle;
  -webkit-font-smoothing: antialiased;
  /* 在chrome浏览器下被加粗的解决办法 */
}
